<template>
  <div class="pa-md-6 pa-sm-3">
    <v-card>
      <v-card-title class="py-2">Data Bank</v-card-title>
      <div v-if="isLoading" class="pa-3">
        <Loader type="sentences" />
      </div>
      <v-form v-else v-model="valid" ref="form">
        <v-card-text>
          <v-text-field
            v-model="dataBank.jenis"
            label="Nama Bank"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>            
          <v-text-field
            v-model="dataBank.nama"
            label="Atas Nama"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>            
          <v-text-field
            v-model="dataBank.norek"
            label="Nomor Rekening"
            type="number"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>            
          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoadingBtn"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </div>
</template>

<script>
  import { mapState } from "vuex";
  import Notif from '@/components/Notif'
  import Loader from '@/components/Loader'

	export default{
		data: () => ({
      valid: true,
      requiredRule: [v => !!v || 'Data harus diisi'],
      msg: {
				success: 'Berhasil disimpan',
				error: 'Gagal disimpan',
				visible: false
			},
    }),

    components: {
      Notif,
      Loader
    },
    
    created() {
      this.$store.dispatch('dataAdminBank/getDataBank')
      this.$store.dispatch('dataAdminBank/updateIsLoading', true)
    },

    computed: {
      ...mapState('dataAdminBank', ['isLoading', 'dataBank', 'status', 'isLoadingBtn'])
    },

		methods: {
      validate () {
        if (this.$refs.form.validate()) {
          const data = {
            id: this.dataBank.id,
            nama: this.dataBank.nama,
            jenis: this.dataBank.jenis,
            norek: this.dataBank.norek
          }
          this.$store.dispatch('dataAdminBank/putDataBank', data)
          this.$store.dispatch('dataAdminBank/updateIsLoadingBtn', true)
          this.msg.visible = true
        }
      },
      visible(val) {
				this.msg.visible = val
				this.$store.commit('dataAdminBank/updateStatus', null)
			}
    }
	}
</script>